/* src/HomePage.css */
.home-container {
    display: flex;
    height: 100vh;
}

.sidebar {
    width: 250px;
    background-color: #2c3e50; /* Darker background */
    color: white;
    padding: 20px;
}

.main-content {
    flex-grow: 1;
    padding: 20px;
    background-color: #ecf0f1; /* Light background */
}

.sidebar ul {
    list-style-type: none;
    padding: 0;
}

.sidebar li {
    margin: 15px 0;
}

.sidebar li:hover {
    color: #3498db; /* Change color on hover */
}
